/**
 * Autosave status indicator for settings pages
 */
import type { AutosaveStatus as Status } from '../../../hooks/useSettingsAutosave';

export interface AutosaveStatusProps {
  status: Status;
  error?: string | null;
}

export function AutosaveStatus({ status, error }: AutosaveStatusProps) {
  if (status === 'idle') return null;

  return (
    <div className={`cedros-autosave-status cedros-autosave-status--${status}`}>
      {status === 'pending' && (
        <>
          <span className="cedros-autosave-dot" />
          <span>Unsaved changes</span>
        </>
      )}
      {status === 'saving' && (
        <>
          <span className="cedros-autosave-spinner" />
          <span>Saving...</span>
        </>
      )}
      {status === 'saved' && (
        <>
          <span className="cedros-autosave-check">&#10003;</span>
          <span>Saved</span>
        </>
      )}
      {status === 'error' && (
        <>
          <span className="cedros-autosave-error-icon">!</span>
          <span>{error || 'Save failed'}</span>
        </>
      )}
    </div>
  );
}
