/**
 * Settings input components - reusable inputs for system settings
 */
import { useState, useCallback, useMemo, type ReactNode } from 'react';
import type { SystemSetting, SettingMeta } from '../../../types';
import { formatDuration, SETTING_METADATA, PRESET_TOKEN_SYMBOLS } from './settingsMetadata';

/** UI-01: Safely render description text that may contain simple <a> tags */
function renderDescription(html: string): ReactNode {
  // Split on <a href="...">...</a> pattern, render links as React elements
  const parts = html.split(/(<a\s[^>]*>.*?<\/a>)/g);
  if (parts.length === 1) return html;
  return parts.map((part, i) => {
    const match = part.match(/^<a\s+href="([^"]+)"[^>]*>([^<]+)<\/a>$/);
    if (match) {
      return (
        <a key={i} href={match[1]} target="_blank" rel="noopener noreferrer">
          {match[2]}
        </a>
      );
    }
    return part;
  });
}

// =============================================================================
// SETTINGS SECTION
// =============================================================================

export interface SettingsSectionProps {
  settings: SystemSetting[];
  edits: Record<string, string>;
  onChange: (key: string, value: string) => void;
  /** External warnings keyed by setting key */
  externalWarnings?: Record<string, string>;
}

export function SettingsSection({
  settings,
  edits,
  onChange,
  externalWarnings,
}: SettingsSectionProps) {
  return (
    <div className="cedros-settings-grid">
      {settings.map((setting) => (
        <SettingRow
          key={setting.key}
          setting={setting}
          editValue={edits[setting.key]}
          onChange={onChange}
          externalWarning={externalWarnings?.[setting.key]}
        />
      ))}
    </div>
  );
}

// =============================================================================
// SETTING ROW
// =============================================================================

interface SettingRowProps {
  setting: SystemSetting;
  editValue?: string;
  onChange: (key: string, value: string) => void;
  /** External warning message that overrides threshold-based warnings */
  externalWarning?: string;
}

export function SettingRow({ setting, editValue, onChange, externalWarning }: SettingRowProps) {
  const meta = SETTING_METADATA[setting.key];
  const currentValue = editValue ?? setting.value;
  const hasChange = editValue !== undefined && editValue !== setting.value;
  const isBoolean = meta?.inputType === 'boolean';

  // Check for warnings (external warnings take precedence)
  const warning = useMemo(() => {
    if (externalWarning) return externalWarning;
    if (!meta?.warningThreshold) return null;
    const numValue = parseInt(currentValue, 10);
    if (isNaN(numValue)) return null;
    const { above, below, message } = meta.warningThreshold;
    if (above !== undefined && numValue > above) return message;
    if (below !== undefined && numValue < below) return message;
    return null;
  }, [currentValue, meta?.warningThreshold, externalWarning]);

  if (!meta) {
    // Fallback for unknown settings
    return (
      <div className={`cedros-setting-row ${hasChange ? 'cedros-setting-row-changed' : ''}`}>
        <div className="cedros-setting-label">
          <span className="cedros-setting-name">{setting.key}</span>
          {setting.description && (
            <span className="cedros-setting-description">{setting.description}</span>
          )}
        </div>
        <div className="cedros-setting-input-wrapper">
          <input
            type="text"
            value={currentValue}
            onChange={(e) => onChange(setting.key, e.target.value)}
            className="cedros-setting-input"
          />
        </div>
      </div>
    );
  }

  return (
    <div
      className={`cedros-setting-row ${hasChange ? 'cedros-setting-row-changed' : ''} ${warning ? 'cedros-setting-row-warning' : ''} ${isBoolean ? 'cedros-setting-row-toggle' : ''}`}
    >
      {isBoolean ? (
        <>
          <div className="cedros-setting-control cedros-setting-control-toggle">
            <SettingInput
              meta={meta}
              value={currentValue}
              onChange={(value) => onChange(setting.key, value)}
            />
            {warning && <div className="cedros-setting-warning">{warning}</div>}
          </div>
          <div className="cedros-setting-label">
            <span className="cedros-setting-name">{meta.label}</span>
            <span className="cedros-setting-description">
              {renderDescription(meta.description)}
            </span>
          </div>
        </>
      ) : (
        <>
          <div className="cedros-setting-label">
            <span className="cedros-setting-name">{meta.label}</span>
            <span className="cedros-setting-description">
              {renderDescription(meta.description)}
            </span>
          </div>

          <div className="cedros-setting-control">
            <SettingInput
              meta={meta}
              value={currentValue}
              onChange={(value) => onChange(setting.key, value)}
            />
            {warning && <div className="cedros-setting-warning">{warning}</div>}
          </div>
        </>
      )}
    </div>
  );
}

// =============================================================================
// SETTING INPUT - Renders the appropriate input based on type
// =============================================================================

interface SettingInputProps {
  meta: SettingMeta;
  value: string;
  onChange: (value: string) => void;
}

function SettingInput({ meta, value, onChange }: SettingInputProps) {
  switch (meta.inputType) {
    case 'duration':
      return (
        <DurationInput value={value} onChange={onChange} presets={meta.presets} min={meta.min} />
      );
    case 'percentage':
      return (
        <PercentageInput
          value={value}
          onChange={onChange}
          min={meta.min ?? 1}
          max={meta.max ?? 100}
          step={meta.step ?? 5}
          presets={meta.presets}
        />
      );
    case 'select':
      return (
        <SelectInput
          value={value}
          onChange={onChange}
          presets={meta.presets ?? []}
          unit={meta.unit}
        />
      );
    case 'number':
      return (
        <NumberInput
          value={value}
          onChange={onChange}
          min={meta.min}
          max={meta.max}
          unit={meta.unit}
        />
      );
    case 'tokenList':
      return <TokenListInput value={value} onChange={onChange} />;
    case 'text':
      return (
        <input
          type="text"
          value={value}
          onChange={(e) => onChange(e.target.value)}
          className="cedros-setting-input"
          placeholder={meta.label}
        />
      );
    case 'boolean':
      return <BooleanInput value={value} onChange={onChange} />;
    case 'secret':
      return <SecretInput value={value} onChange={onChange} multiline={meta.multiline} />;
    case 'tokenSymbolList':
      return <TokenSymbolListInput value={value} onChange={onChange} />;
    default:
      return (
        <input
          type="text"
          value={value}
          onChange={(e) => onChange(e.target.value)}
          className="cedros-setting-input"
        />
      );
  }
}

// =============================================================================
// DURATION INPUT
// =============================================================================

interface DurationInputProps {
  value: string;
  onChange: (value: string) => void;
  presets?: { label: string; value: string }[];
  min?: number;
}

export function DurationInput({ value, onChange, presets, min = 0 }: DurationInputProps) {
  const numValue = parseInt(value, 10) || 0;
  const displayValue = formatDuration(numValue);

  const handlePresetChange = useCallback(
    (e: React.ChangeEvent<HTMLSelectElement>) => {
      if (e.target.value) {
        onChange(e.target.value);
      }
    },
    [onChange]
  );

  const handleCustomChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const newValue = Math.max(min, parseInt(e.target.value, 10) || 0);
      onChange(String(newValue));
    },
    [onChange, min]
  );

  return (
    <div className="cedros-duration-input">
      {presets && presets.length > 0 && (
        <select
          value={presets.find((p) => p.value === value)?.value ?? ''}
          onChange={handlePresetChange}
          className="cedros-setting-select"
        >
          <option value="">Custom...</option>
          {presets.map((preset) => (
            <option key={preset.value} value={preset.value}>
              {preset.label}
            </option>
          ))}
        </select>
      )}
      <div className="cedros-duration-custom">
        <input
          type="number"
          value={numValue}
          onChange={handleCustomChange}
          min={min}
          className="cedros-setting-input cedros-setting-input-sm"
        />
        <span className="cedros-setting-unit">seconds</span>
        <span className="cedros-duration-display">= {displayValue}</span>
      </div>
    </div>
  );
}

// =============================================================================
// PERCENTAGE INPUT
// =============================================================================

interface PercentageInputProps {
  value: string;
  onChange: (value: string) => void;
  min: number;
  max: number;
  step: number;
  presets?: { label: string; value: string }[];
}

export function PercentageInput({
  value,
  onChange,
  min,
  max,
  step,
  presets,
}: PercentageInputProps) {
  const numValue = parseInt(value, 10) || min;

  const handleSliderChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      onChange(e.target.value);
    },
    [onChange]
  );

  const handlePresetClick = useCallback(
    (presetValue: string) => {
      onChange(presetValue);
    },
    [onChange]
  );

  return (
    <div className="cedros-percentage-input">
      <div className="cedros-percentage-slider-row">
        <input
          type="range"
          value={numValue}
          onChange={handleSliderChange}
          min={min}
          max={max}
          step={step}
          className="cedros-percentage-slider"
        />
        <span className="cedros-percentage-value">{numValue}%</span>
      </div>
      {presets && presets.length > 0 && (
        <div className="cedros-preset-buttons">
          {presets.map((preset) => (
            <button
              key={preset.value}
              type="button"
              className={`cedros-preset-button ${preset.value === value ? 'cedros-preset-button-active' : ''}`}
              onClick={() => handlePresetClick(preset.value)}
            >
              {preset.label}
            </button>
          ))}
        </div>
      )}
    </div>
  );
}

// =============================================================================
// SELECT INPUT
// =============================================================================

interface SelectInputProps {
  value: string;
  onChange: (value: string) => void;
  presets: { label: string; value: string }[];
  unit?: string;
}

export function SelectInput({ value, onChange, presets, unit }: SelectInputProps) {
  const isCustomValue = !presets.some((p) => p.value === value);

  const handleSelectChange = useCallback(
    (e: React.ChangeEvent<HTMLSelectElement>) => {
      if (e.target.value !== '__custom__') {
        onChange(e.target.value);
      }
    },
    [onChange]
  );

  const handleCustomChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      onChange(e.target.value);
    },
    [onChange]
  );

  return (
    <div className="cedros-select-input">
      <select
        value={isCustomValue ? '__custom__' : value}
        onChange={handleSelectChange}
        className="cedros-setting-select"
      >
        {presets.map((preset) => (
          <option key={preset.value} value={preset.value}>
            {preset.label}
          </option>
        ))}
        <option value="__custom__">Custom...</option>
      </select>
      {isCustomValue && (
        <div className="cedros-select-custom">
          <input
            type="number"
            value={value}
            onChange={handleCustomChange}
            className="cedros-setting-input cedros-setting-input-sm"
          />
          {unit && <span className="cedros-setting-unit">{unit}</span>}
        </div>
      )}
    </div>
  );
}

// =============================================================================
// NUMBER INPUT
// =============================================================================

interface NumberInputProps {
  value: string;
  onChange: (value: string) => void;
  min?: number;
  max?: number;
  unit?: string;
}

export function NumberInput({ value, onChange, min, max, unit }: NumberInputProps) {
  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      onChange(e.target.value);
    },
    [onChange]
  );

  return (
    <div className="cedros-number-input">
      <input
        type="number"
        value={value}
        onChange={handleChange}
        min={min}
        max={max}
        className="cedros-setting-input"
      />
      {unit && <span className="cedros-setting-unit">{unit}</span>}
    </div>
  );
}

// =============================================================================
// BOOLEAN INPUT (Toggle)
// =============================================================================

interface BooleanInputProps {
  value: string;
  onChange: (value: string) => void;
}

export function BooleanInput({ value, onChange }: BooleanInputProps) {
  const isEnabled = value === 'true';

  const handleToggle = useCallback(() => {
    onChange(isEnabled ? 'false' : 'true');
  }, [isEnabled, onChange]);

  return (
    <button
      type="button"
      role="switch"
      aria-checked={isEnabled}
      className={`cedros-toggle ${isEnabled ? 'cedros-toggle-on' : 'cedros-toggle-off'}`}
      onClick={handleToggle}
    >
      <span className="cedros-toggle-track">
        <span className="cedros-toggle-thumb" />
      </span>
      <span className="cedros-toggle-label">{isEnabled ? 'Enabled' : 'Disabled'}</span>
    </button>
  );
}

// =============================================================================
// SECRET INPUT (Masked)
// =============================================================================

interface SecretInputProps {
  value: string;
  onChange: (value: string) => void;
  multiline?: boolean;
}

export function SecretInput({ value, onChange, multiline }: SecretInputProps) {
  const [isEditing, setIsEditing] = useState(false);
  const [showValue, setShowValue] = useState(false);
  const hasValue = value && value.length > 0;

  const handleEdit = useCallback(() => {
    setIsEditing(true);
    setShowValue(true);
  }, []);

  const handleDone = useCallback(() => {
    setIsEditing(false);
    setShowValue(false);
  }, []);

  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
      onChange(e.target.value);
    },
    [onChange]
  );

  if (!isEditing && hasValue) {
    return (
      <div className="cedros-secret-input cedros-secret-input-masked">
        <span className="cedros-secret-masked">{''.repeat(Math.min(value.length, 20))}</span>
        <button type="button" className="cedros-secret-edit-btn" onClick={handleEdit}>
          Edit
        </button>
      </div>
    );
  }

  return (
    <div className="cedros-secret-input">
      {multiline ? (
        <textarea
          value={value}
          onChange={handleChange}
          className="cedros-setting-input cedros-setting-textarea"
          placeholder="Enter secret value..."
          rows={4}
        />
      ) : (
        <input
          type={showValue ? 'text' : 'password'}
          value={value}
          onChange={handleChange}
          className="cedros-setting-input"
          placeholder="Enter secret value..."
        />
      )}
      <div className="cedros-secret-actions">
        {!multiline && (
          <button
            type="button"
            className="cedros-secret-toggle-btn"
            onClick={() => setShowValue(!showValue)}
          >
            {showValue ? 'Hide' : 'Show'}
          </button>
        )}
        {isEditing && (
          <button type="button" className="cedros-secret-done-btn" onClick={handleDone}>
            Done
          </button>
        )}
      </div>
    </div>
  );
}

// =============================================================================
// TOKEN LIST INPUT
// =============================================================================

interface TokenDefinition {
  symbol: string;
  mint: string;
  decimals: number;
  logoUrl?: string;
}

interface TokenListInputProps {
  value: string;
  onChange: (value: string) => void;
}

export function TokenListInput({ value, onChange }: TokenListInputProps) {
  const tokens: TokenDefinition[] = useMemo(() => {
    try {
      return JSON.parse(value || '[]');
    } catch {
      return [];
    }
  }, [value]);

  const updateTokens = useCallback(
    (updated: TokenDefinition[]) => {
      onChange(JSON.stringify(updated));
    },
    [onChange]
  );

  const addToken = useCallback(() => {
    updateTokens([...tokens, { symbol: '', mint: '', decimals: 6 }]);
  }, [tokens, updateTokens]);

  const updateToken = useCallback(
    (index: number, field: keyof TokenDefinition, val: string | number | undefined) => {
      const updated = [...tokens];
      updated[index] = { ...updated[index], [field]: val };
      updateTokens(updated);
    },
    [tokens, updateTokens]
  );

  const removeToken = useCallback(
    (index: number) => {
      updateTokens(tokens.filter((_, i) => i !== index));
    },
    [tokens, updateTokens]
  );

  return (
    <div className="cedros-token-list-input">
      {/* Preset tokens info */}
      <div className="cedros-token-presets">
        <span className="cedros-token-presets-label">Built-in tokens:</span>
        <div className="cedros-token-presets-list">
          {PRESET_TOKEN_SYMBOLS.map((symbol) => (
            <span key={symbol} className="cedros-token-preset-chip">
              {symbol}
            </span>
          ))}
        </div>
      </div>

      {tokens.length === 0 && (
        <p className="cedros-token-list-empty">
          No custom tokens added. Use the built-in tokens above or add your own.
        </p>
      )}

      {tokens.map((token, i) => (
        <div key={i} className="cedros-token-row">
          <div className="cedros-token-row-fields">
            <input
              type="text"
              placeholder="Symbol"
              value={token.symbol}
              onChange={(e) => updateToken(i, 'symbol', e.target.value.toUpperCase())}
              className="cedros-setting-input cedros-token-input-symbol"
              maxLength={10}
            />
            <input
              type="text"
              placeholder="Mint address"
              value={token.mint}
              onChange={(e) => updateToken(i, 'mint', e.target.value)}
              className="cedros-setting-input cedros-token-input-mint"
            />
            <input
              type="number"
              placeholder="Decimals"
              value={token.decimals}
              onChange={(e) => updateToken(i, 'decimals', parseInt(e.target.value, 10) || 0)}
              className="cedros-setting-input cedros-token-input-decimals"
              min={0}
              max={18}
            />
            <input
              type="text"
              placeholder="Logo URL (optional)"
              value={token.logoUrl || ''}
              onChange={(e) => updateToken(i, 'logoUrl', e.target.value || undefined)}
              className="cedros-setting-input cedros-token-input-logo"
            />
          </div>
          <button
            type="button"
            className="cedros-token-remove-btn"
            onClick={() => removeToken(i)}
            title="Remove token"
          >
            
          </button>
        </div>
      ))}

      <button type="button" className="cedros-token-add-btn" onClick={addToken}>
        + Add Token
      </button>
    </div>
  );
}

// =============================================================================
// TOKEN SYMBOL LIST INPUT
// =============================================================================

interface TokenSymbolListInputProps {
  value: string;
  onChange: (value: string) => void;
}

export function TokenSymbolListInput({ value, onChange }: TokenSymbolListInputProps) {
  const symbols = useMemo(() => {
    return value
      .split(',')
      .map((s) => s.trim())
      .filter(Boolean);
  }, [value]);

  const addSymbol = useCallback(
    (symbol: string) => {
      if (!symbol || symbols.includes(symbol)) return;
      const updated = [...symbols, symbol].join(', ');
      onChange(updated);
    },
    [symbols, onChange]
  );

  const removeSymbol = useCallback(
    (symbol: string) => {
      const updated = symbols.filter((s) => s !== symbol).join(', ');
      onChange(updated);
    },
    [symbols, onChange]
  );

  return (
    <div className="cedros-token-symbol-list-input">
      {/* Preset tokens - click to add */}
      <div className="cedros-token-presets">
        <span className="cedros-token-presets-label">Click to add:</span>
        <div className="cedros-token-presets-list">
          {PRESET_TOKEN_SYMBOLS.map((symbol) => {
            const isSelected = symbols.includes(symbol);
            return (
              <button
                key={symbol}
                type="button"
                className={`cedros-token-preset-chip ${isSelected ? 'cedros-token-preset-chip-selected' : ''}`}
                onClick={() => (isSelected ? removeSymbol(symbol) : addSymbol(symbol))}
                title={isSelected ? `Remove ${symbol}` : `Add ${symbol}`}
              >
                {symbol}
                {isSelected && <span className="cedros-token-chip-check"></span>}
              </button>
            );
          })}
        </div>
      </div>

      {/* Text input for manual entry or custom symbols */}
      <input
        type="text"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className="cedros-setting-input"
        placeholder="USDC, SOL, BONK..."
      />
    </div>
  );
}
